.Thumbnail {
  position: relative;
}

a * {
  transition: 0.25s ease-out;
}

a:hover {
  cursor: pointer;
}

img {
  width: 100%;
  height: auto;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.overlay {
  opacity: 0;
  position: absolute;
  inset: 0;
  bottom: 2px;
  background-color: black;
}

a:hover .overlay {
  opacity: 0.9;
}

.title {
  font-size: 1.5rem;
  opacity: 0;
  color: white;
  position: absolute;
  top: 1rem;
  left: 1rem;
}

> a:hover .title {
  opacity: 1;
}

.play-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
}

.play-icon {
  position: relative;
  height: 5rem;
  width: 5rem;
  left: -50%;

  top: -2.5rem;
  opacity: 0;
}

a:hover .play-icon {
  opacity: 1;
}
